$small-range: (0px, 640px);
$medium-range: (641px, 1023px);
$large-range: (1024px, 1440px);
$xlarge-range: (1441px, 1920px);
$xxlarge-range: (1921px, 99999999em);

$landscape: "only screen and (orientation: landscape)";
$portrait: "only screen and (orientation: portrait)";

$small-up: "only screen";
$small-only: "only screen and (max-width: upper-bound($small-range))";

$medium-up: "only screen and (min-width:lower-bound($medium-range))";
$medium-only: "only screen and (min-width:lower-bound($medium-range)) and (max-width:upper-bound($medium-range))";

$large-up: "only screen and (min-width:lower-bound($large-range))";
$large-only: "only screen and (min-width:lower-bound($large-range)) and (max-width:upper-bound($large-range))";

$xlarge-up: "only screen and (min-width:lower-bound($xlarge-range))";
$xlarge-only: "only screen and (min-width:lower-bound($xlarge-range)) and (max-width:upper-bound($xlarge-range))";

$xxlarge-up: "only screen and (min-width:lower-bound($xxlarge-range))";
$xxlarge-only: "only screen and (min-width:lower-bound($xxlarge-range)) and (max-width:upper-bound($xxlarge-range))";

.wrapper {
	> .inner-wrapper {
		margin-left: $width-gutter/2;
		margin-right: $width-gutter/2;
	}
}

.row {
    position: relative;
    width: 100%;
    max-width: $width-site;
    margin-left: auto;
    margin-right: auto;

    &.full {
        max-width: 100%;
    }

    &.flush {
        .column,
        .columns {
            &:first-child {
                padding-left: 0;
            }
            &:last-child {
                padding-right: 0;
            }
        }
    }

    &:before, &:after {
        content: " ";
        display: table;
        clear: both;
	}
}

.column,
.columns {
    float: left;
    width: 100%;
    padding: 0 $width-gutter/2;
    @include box-sizing(border-box);

    &.float {
        width: auto;
    }

    .row {
        width: auto;
        margin-left: -$width-gutter;
        margin-right: -$width-gutter;
    }

	&:last-child {
		float: right;
	}
}

