[class*="block-grid"] {
    display: block;
    padding: 0;
    margin: 0 (-($width-gutter/2));
    *zoom: 1;

    &:before,
	&:after {
	    content: " ";
	    display: table
	}

	&:after {
	    clear: both
	}

	> li {
	    display: block;
	    height: auto;
	    float: left;
	    padding: 0 $width-gutter/2 $width-gutter;

		@include box-sizing(border-box);
	}
}

